.page {
  overflow: hidden;


  @include breakpoint($tablet-bp) {

    .show-sidebar & {

      position: relative;

      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;

        background-color: rgba($black, .9);

        position: absolute;
        top: 0;
        left: 0;
        z-index: 100;
      }
    }
  }

}

.page__title {
  font-size: 2.1rem;
  color: $white;
  margin-bottom: 3rem;
}
