.post {
  margin-bottom: 3rem;
  padding: 2.5rem;
  background-color: $primary;
  box-shadow: 0 15px 25px rgba($black, .35);
  border-radius: 5px;
  overflow: hidden;

  @include breakpoint($mobile-bp) {
    padding: 1.5rem;
  }

}

.post__header {
  margin: 0 0 2.5rem;

  &--preview {
    margin: -2.5rem -2.5rem 2.5rem;
  }
}

.post__preview {
  display: block;
  max-width: 100%;
  height: auto;
}


.post__content {
  margin-bottom: 2rem;
}

.post__title {
  font-size: 1.8rem;
  line-height: 1.4;
  font-weight: 500;
  color: $white;
  margin-bottom: 1.5rem;

  a {
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
  &--open {
    margin-bottom: .75rem;
  }
}
.post__description {
  font-size: 1.4rem;
  color: #D2D2D2;
}

.post__text {
  font-size: 1.4rem;
  color: $white;

  h2, h3, h4, h5, h6 {
    margin-bottom: 2rem;
    font-weight: 700;
  }
  h2 {
    font-size: 1em;
  }

  ul, ol {
    margin-bottom: 2rem;
  }
  ol {
    list-style: decimal;
    list-style-position: inside;
  }

  p {
    margin-bottom: 2rem;
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  a {
    color: $blue;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

}

.post__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;


  @include breakpoint($mobile-bp) {
    flex-direction: column;
    align-items: flex-start;
  }


}

.post__data {
  display: flex;
  color: #828282;
  line-height: 1.2;

  a {
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
  @include breakpoint($mobile-bp) {
    margin-bottom: 1rem;

    font-size: 1.2rem;
  }
}

.post__data-item {

  &:not(:last-child):after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    margin: 0 1.5rem;
    position: relative;
    top: -2px;

    border-radius: 50px;
    background-color: #C4C4C4;
  }
}

.post__read {
  font-size: 1.4rem;
  color: #107EFF;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.post__actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;

  font-size: 1.2rem;
  color: $white;


  a {
    color: inherit;
    text-decoration: none;

    opacity: .6;
    transition: opacity .15s linear;

    &:hover {
      text-decoration: underline;
      opacity: 1;
    }

  }
}

.post__actions-icon {
  margin-left: 1rem;
}
